A,CACA,eAAA,CAEA,gBAEA,CACA,mDACA,kBAAA,CACA,wBAAA,CACA,iBAAA,CAQA,UAAA,CANA,iBAAA,CAGA,cAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAAA,CALA,iBAAA,CAMA,iBAEA,CACA,kDASA,+BAAA,CARA,+CAAA,CAEA,0BAAA,CACA,4BAAA,CAFA,2BAAA,CAKA,yBAAA,CACA,iBAAA,CAFA,2BAIA,CACA,oEAMA,aAAA,CALA,mBAAA,CAGA,cAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAEA,CAPA,+DAMA,aAAA,CALA,mBAAA,CAGA,cAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAEA,CACA,oCAIA,eAAA,CACA,yBAAA,CACA,mCAAA,CAJA,YAAA,CAKA,kBAAA,CAJA,YAAA,CAFA,SAOA,CACA,0CAGA,sBAAA,CACA,oBAAA,CAHA,SAIA,CACA,6CAMA,UAAA,CALA,mBAAA,CAGA,cAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAEA,CACA,6CAMA,aAAA,CALA,mBAAA,CAGA,cAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAAA,CAEA,kBACA,CACA,qCACA,YACA,CACA,sDACA,WACA,CACA,0DAGA,UAAA,CAFA,iBAAA,CACA,UAEA,CACA,0CAMA,aAAA,CALA,mBAAA,CAGA,kBAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAEA,CACA,qDACA,UACA,CACA,4CAMA,UAAA,CALA,mBAAA,CAGA,mBAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAEA,CACA,kCACA,YAAA,CACA,cACA,CACA,uCAQA,kBAAA,CACA,iBAAA,CAHA,aAAA,CALA,mBAAA,CAGA,mBAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAAA,CAMA,iBAAA,CADA,gBAAA,CAHA,eAKA,CACA,mCACA,YAAA,CACA,6BACA,CACA,6BACA,YACA,CACA,6CACA,iBAIA,CACA,2FAJA,YAAA,CACA,qBAAA,CACA,6BAMA,CACA,qDACA,gBACA,CACA,iDAMA,aAAA,CALA,mBAAA,CAGA,mBAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAEA,CACA,kDACA,aACA,CACA,uDAMA,UAAA,CALA,mBAAA,CAGA,mBAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAEA,CACA,0CAGA,kBAAA,CAFA,WAAA,CAGA,iBAAA,CAOA,UAAA,CANA,mBAAA,CAGA,mBAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAAA,CAPA,YAAA,CAQA,YAEA,CACA,yCACA,wCACA,eACA,CACA,sCAGA,YAAA,CADA,kBAAA,CADA,eAGA,CACA,CACA,yCACA,oCACA,UACA,CACA,kCACA,cAAA,CACA,sBACA,CACA,sCACA,UACA,CACA,CACA,yCACA,oCAIA,cAAA,CAHA,UACA,CAIA,sCACA,oBACA,CACA,wCAEA,WAAA,CADA,mBAEA,CACA,CACA,yCAIA,wCAEA,WAAA,CADA,mBAEA,CACA,4CACA,mBAAA,CACA,gBACA,CACA,2CACA,cAAA,CACA,gBACA,CACA,oCAEA,kBAAA,CADA,mBAEA,CACA,gCACA,cAAA,CACA,sBACA,CACA","sourcesContent":["<template>\n    <div style=\"margin-bottom: 100px; min-height: 70vh\">\n        <div class=\"venue-search-cont\">\n            <img src=\"/assets/venues/Frame540.png\" alt=\"\" />\n            <div class=\"venue-search-cont-info container-lg\">\n                <div class=\"search-feilds\">\n                    <multiselect\n                        style=\"width: 35%\"\n                        v-model=\"selectedCity\"\n                        placeholder=\"Select City\"\n                        :options=\"options\"\n                        :show-labels=\"false\"\n                        :allow-empty=\"false\"\n                        ref=\"citySelect\"\n                        ><span slot=\"noResult\"\n                            >No city exists.</span\n                        ></multiselect\n                    >\n                    <multiselect\n                        v-model=\"selectedVenue\"\n                        style=\"width: 35%\"\n                        :options=\"venueTypes\"\n                        placeholder=\"Venue Type\"\n                        :show-labels=\"false\"\n                        :allow-empty=\"false\"\n                        ref=\"venueSelect\"\n                        ><span slot=\"noResult\"\n                            >No venue type exists.</span\n                        ></multiselect\n                    >\n                    <button @click=\"searchVenues()\" type=\"button\">\n                        Search\n                    </button>\n                </div>\n            </div>\n        </div>\n        <div class=\"venue-filter-cont\">\n            <div class=\"container-lg\">\n                <div\n                    v-for=\"(item, index) in Object.keys(filters)\"\n                    :key=\"index\"\n                    class=\"venue-filter-single\"\n                >\n                    <span @click.stop=\"closeAllFilters(item)\"\n                        >{{ filters[item].name }}\n                        <img src=\"/assets/venues/chevron-down.svg\" alt=\"\"\n                    /></span>\n                    <div @click.stop v-if=\"filters[item].show\">\n                        <div\n                            v-for=\"(ele, index) in filters[item].filterList\"\n                            :key=\"index\"\n                        >\n                            <label :for=\"ele\"\n                                >{{ ele }}\n                                <input\n                                    type=\"checkbox\"\n                                    :id=\"ele\"\n                                    :value=\"ele\"\n                                    v-model=\"filters[item].selected\"\n                                />\n                            </label>\n                        </div>\n                    </div>\n                </div>\n            </div>\n        </div>\n        <div class=\"venue-head-list container-lg\">\n            <a href=\"/\">Home > </a\n            ><a href=\"/wedding-venues\">Wedding venues > </a\n            ><a\n                v-if=\"type\"\n                :href=\"'/directory/vendors/venues/filtered?type=' + type\"\n                >{{ type }} >\n            </a>\n            <span v-if=\"city\">{{ city }} </span>\n        </div>\n        <div class=\"container-lg\">\n            <div class=\"venue-list-head\">\n                <div class=\"venue-list-details\">\n                    <h3>Wedding Venues</h3>\n                    <h6>\n                        Showing {{ venues.length }} results as per your search\n                        criteria\n                    </h6>\n                </div>\n                <div class=\"venue-list-details-search\">\n                    <button>Select</button>\n                    <input\n                        type=\"text\"\n                        name=\"\"\n                        id=\"\"\n                        placeholder=\"Search for venue by name\"\n                    />\n                </div>\n            </div>\n            <div class=\"venue-list-all\">\n                <div\n                    v-for=\"(item, index) in pageOfItems\"\n                    :key=\"index\"\n                    class=\"venue-list-single\"\n                >\n                    <div style=\"width: 40%\">\n                        <div\n                            :id=\"'main-slider' + item.id\"\n                            class=\"splide main-splide\"\n                        >\n                            <div class=\"splide__track\">\n                                <ul class=\"splide__list\">\n                                    <li class=\"splide__slide\">\n                                        <img\n                                            src=\"/assets/venues/Rectangle5320.png\"\n                                        />\n                                    </li>\n                                    <li class=\"splide__slide\">\n                                        <img\n                                            src=\"/assets/venues/Rectangle5319.png\"\n                                        />\n                                    </li>\n                                    <li class=\"splide__slide\">\n                                        <img\n                                            src=\"/assets/venues/Rectangle5321.png\"\n                                        />\n                                    </li>\n                                    <li class=\"splide__slide\">\n                                        <img\n                                            src=\"/assets/venues/Rectangle5322.png\"\n                                        />\n                                    </li>\n                                    <li class=\"splide__slide\">\n                                        <img\n                                            src=\"/assets/venues/Rectangle5319.png\"\n                                        />\n                                    </li>\n                                </ul>\n                            </div>\n                        </div>\n                        <div :id=\"'thumbnail-slider' + item.id\" class=\"splide\">\n                            <div class=\"splide__track\">\n                                <ul class=\"splide__list\">\n                                    <li class=\"splide__slide\">\n                                        <img\n                                            src=\"/assets/venues/Rectangle5320.png\"\n                                        />\n                                    </li>\n                                    <li class=\"splide__slide\">\n                                        <img\n                                            src=\"/assets/venues/Rectangle5319.png\"\n                                        />\n                                    </li>\n                                    <li class=\"splide__slide\">\n                                        <img\n                                            src=\"/assets/venues/Rectangle5321.png\"\n                                        />\n                                    </li>\n                                    <li class=\"splide__slide\">\n                                        <img\n                                            src=\"/assets/venues/Rectangle5322.png\"\n                                        />\n                                    </li>\n                                    <li class=\"splide__slide\">\n                                        <img\n                                            src=\"/assets/venues/Rectangle5319.png\"\n                                        />\n                                    </li>\n                                </ul>\n                            </div>\n                        </div>\n                    </div>\n                    <a\n                        :href=\"'/directory/vendor/venues/' + item.slug\"\n                        class=\"venue-list-single-descp\"\n                    >\n                        <h1>{{ item.name }}</h1>\n                        <h2>Blue Bay Beach Resort, State Highway 49, ...</h2>\n                        <div>\n                            <div class=\"venue-feature-list\">\n                                <span>\n                                    <img\n                                        src=\"/assets/venues/fluent_people-community-28-regular.svg\"\n                                        alt=\"\"\n                                    />\n                                    Hall capacity\n                                </span>\n                                <span>750</span>\n                            </div>\n                            <div class=\"venue-feature-list\">\n                                <span>\n                                    <img\n                                        src=\"/assets/venues/la_wine-glass-alt.svg\"\n                                        alt=\"\"\n                                    />\n                                    Dining Capacity\n                                </span>\n                                <span>250</span>\n                            </div>\n                            <div class=\"venue-feature-list\">\n                                <span>\n                                    <img\n                                        src=\"/assets/venues/fluent_food-16-regular.svg\"\n                                        alt=\"\"\n                                    />\n                                    Food type\n                                </span>\n                                <span>Veg</span>\n                            </div>\n                            <div class=\"venue-feature-list\">\n                                <span>\n                                    <img\n                                        src=\"/assets/venues/cil_room.svg\"\n                                        alt=\"\"\n                                    />\n                                    Rooms\n                                </span>\n                                <span>10 Rooms A/C </span>\n                            </div>\n                            <div class=\"venue-feature-list\">\n                                <span>\n                                    <img\n                                        src=\"/assets/venues/cil_room.svg\"\n                                        alt=\"\"\n                                    />\n                                    Areas Available\n                                </span>\n                                <span>Lawn, A/C Hall, Mandap</span>\n                            </div>\n                        </div>\n                        <p>\n                            Blue Bay Beach Resort is a beachfront resort. The\n                            wedding venue is designed in a brilliant way that\n                            would add value to the\n                        </p>\n                        <div class=\"venue-tags-cont\">\n                            <span>Wedding Hotels</span>\n                            <span>Banquet Halls</span>\n                            <span>Indoor</span>\n                            <span>Outdoor</span>\n                        </div>\n                        <div class=\"venue-price-food\">\n                            <div class=\"venue-food\">\n                                <div>\n                                    <div>\n                                        <img\n                                            src=\"/assets/venues/nonveg.svg\"\n                                            alt=\"\"\n                                        />\n                                        Non- Vegeterian\n                                    </div>\n                                    <div>\n                                        <img\n                                            src=\"/assets/venues/veg.svg\"\n                                            alt=\"\"\n                                        />\n                                        Vegeterian\n                                    </div>\n                                </div>\n                                <div>\n                                    <div><span>₹1200</span>/Plate</div>\n                                    <div><span>₹950</span>/Plate</div>\n                                </div>\n                            </div>\n                            <button>Check availability</button>\n                        </div>\n                    </a>\n                </div>\n            </div>\n        </div>\n        <div style=\"display: flex; justify-content: center; margin-bottom: 1em\">\n            <jw-pagination\n                :items=\"venues\"\n                :pageSize=\"3\"\n                @changePage=\"onChangePage\"\n            ></jw-pagination>\n        </div>\n    </div>\n</template>\n\n<script>\nimport Multiselect from \"vue-multiselect\";\n\nexport default {\n    components: {\n        Multiselect,\n    },\n    props: [\"venues\", \"cities\", \"type\", \"city\"],\n    data() {\n        return {\n            selectedVenue: this.type ?? null,\n            selectedCity: this.city ?? null,\n            options: this.cities,\n            venueTypes: [\n                \"Resorts\",\n                \"Destination\",\n                \"Beach\",\n                \"Hotels\",\n                \"Banquet Halls\",\n                \"Lawns\",\n                \"Community Center\",\n            ],\n            filters: {\n                price: {\n                    name: \"Price per plate\",\n                    show: false,\n                    selected: [],\n                    filterList: [\"1000\", \"2000\", \"3000\"],\n                },\n                locality: {\n                    name: \"Locality\",\n                    show: false,\n                    selected: [],\n                    filterList: [\"Chandigarh\", \"Panchkula\", \"Mohali\"],\n                },\n                guests: {\n                    name: \"No. of guests\",\n                    show: false,\n                    selected: [],\n                    filterList: [\"200\", \"400\", \"600\"],\n                },\n                space: {\n                    name: \"Space preference\",\n                    show: false,\n                    selected: [],\n                    filterList: [\"Outdoor\", \"Indoor\", \"Poolisde\"],\n                },\n                ratings: {\n                    name: \"Ratings\",\n                    show: false,\n                    selected: [],\n                    filterList: [\"5 stars\", \"4 stars\", \"3 stars\"],\n                },\n            },\n            pageOfItems: [],\n            splider: {},\n            mainSplider: {},\n        };\n    },\n    methods: {\n        onChangePage(pageOfItems) {\n            // update page of items\n            this.pageOfItems = pageOfItems;\n            this.setSplider();\n        },\n        searchVenues() {\n            if (this.selectedCity == null) {\n                this.$refs.citySelect.activate();\n                return;\n            }\n            if (this.selectedVenue == null) {\n                this.$refs.venueSelect.activate();\n                return;\n            }\n            window.location.href = `/directory/vendors/venues/filtered?type=${this.selectedVenue}&city=${this.selectedCity}`;\n        },\n        closeAllFilters(item) {\n            for (let keys in this.filters) {\n                if (keys == item) {\n                    continue;\n                }\n                this.filters[keys].show = false;\n            }\n            this.filters[item].show = !this.filters[item].show;\n        },\n        closeFilters() {\n            for (let keys in this.filters) {\n                this.filters[keys].show = false;\n            }\n        },\n        setSplider() {\n            let _this = this;\n            this.$nextTick(function () {\n                this.pageOfItems.forEach(function (item, index) {\n                    if (\n    